@charset "UTF-8";
body {
  background: #fff; }

/*selection*/
::selection {
  background: rgba(255, 0, 0, 0.7);
  color: #fff;
  text-shadow: none; }

/*clearFix clear*/
.clearFix:after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden; }

.clearFix {
  zoom: 1; }

.clear {
  clear: both; }

/*link*/
a {
  text-decoration: none;
  cursor: pointer;
  font-family: \5FAE\8F6F\96C5\9ED1, Arial, Helvetica, sans-serif; }

a:link {
  color: #000; }

a:visited {
  color: #000; }

a:active {
  color: #f25e5e;
  star: expression(this.onFocus=this.blur());
  background-color: transparent; }

a:hover {
  color: #f25e5e;
  text-decoration: none; }

a.important {
  color: #98a1ab; }

/*webheader*/
.webheader {
  width: 100%;
  height: 110px;
  background: url("../../images/bg_webheader.gif") 0 0 repeat-x;
  margin-bottom: 40px; }

.webheader .inner {
  width: 1200px;
  height: 98px;
  margin: 0 auto;
  background: url("../../images/bg_webheader_line.gif") 0 0 repeat-x;
  padding-top: 12px;
  position: relative; }

.webheader .inner .logo {
  display: block;
  width: 110px;
  height: 85px;
  background: url("../../images/logo.jpg");
  float: left;
  margin-right: 30px; }

.webheader .inner .nav {
  float: left;
  padding-top: 28px; }

.webheader .inner .nav ul {
  height: 70px;
  overflow: hidden; }

.webheader .inner .nav ul li {
  float: left;
  background: url("../../images/nav_webheader_line.gif") 0 6px no-repeat;
  padding: 0 5px 0 6px;
  height: 70px;
  position: relative; }

.webheader .inner .nav ul li.first {
  background: none; }

.webheader .inner .nav ul li a {
  display: block;
  font-size: 16px;
  height: 30px;
  line-height: 30px;
  padding: 0 12px; }

.webheader .inner .search {
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -20px;
  width: 40px;
  height: 40px;
  background: url("../../images/search_webheader.gif"); }

.webheader .inner .nav ul li.c:after {
  content: "";
  display: block;
  width: 90%;
  height: 6px;
  background: url("../../images/line_webheader_currentnav.gif") center center no-repeat;
  position: absolute;
  left: 5%;
  bottom: 4px; }

/*webmain*/
.webmain {
  margin: 0 auto 80px;
  width: 1200px;
  overflow: hidden; }

/*导航栏*/
.navTitle {
  color: #ccc;
  margin-bottom: 30px; }

.navTitle a {
  color: inherit; }

.navTitle a:hover {
  color: red; }

.navTitle .loginfo {
  float: right;
  margin: 0 20px 0 15px; }

.navTitle .loginfo .face {
  display: inline-block;
  float: left;
  width: 20px;
  height: 20px;
  background: url("../../images/icon-logface.jpg") 50% no-repeat; }

.navTitle .loginfo .name {
  display: inline-block;
  float: left;
  height: 20px;
  margin-left: 15px;
  line-height: 20px;
  cursor: pointer; }

.navTitle .loginfo .name:after {
  content: "";
  display: block;
  position: absolute;
  margin-left: 55px;
  margin-top: -10px;
  border-top: 6px solid #000;
  border-right: 6px solid #fff;
  border-left: 6px solid #fff; }

/*标题，抬头css*/
.webmain .con {
  margin-bottom: 45px; }

.webmain .con .title {
  height: 60px;
  overflow: hidden;
  background: url("../../images/line_contitle.gif") center center repeat-x;
  margin-bottom: 15px;
  position: relative; }

.webmain .con .title a.more {
  display: block;
  position: absolute;
  right: 0;
  top: 15px;
  background: url("../../images/more_contitle.gif");
  width: 130px;
  height: 30px;
  text-indent: -99em;
  overflow: hidden; }

.webmain .con .title h2 {
  display: block;
  width: 170px;
  height: 60px;
  background-image: url("../../images/sprite_rice.gif");
  background-repeat: no-repeat;
  text-indent: -99em;
  overflow: hidden;
  float: left; }

.webmain .con .title .group {
  display: inline-block;
  height: 60px;
  background-color: #fff; }

.webmain .con .title .group li {
  float: left;
  width: 60px;
  height: 30px;
  margin-top: 15px;
  line-height: 30px;
  text-align: center;
  cursor: pointer; }
  .webmain .con .title .group li.active {
    background-color: #000;
    color: #fff; }

/*webfooter*/
.webfooter {
  width: 100%;
  height: 160px;
  background: #000; }

.webfooter .inner {
  width: 1200px;
  height: 132px;
  margin: 0 auto;
  padding-top: 28px; }

.webfooter .inner .logo {
  float: left;
  width: 110px;
  height: 85px;
  background: url("../../images/footer_logo.jpg");
  margin-right: 25px; }

.webfooter .inner .info {
  float: left;
  width: 880px;
  padding-top: 20px; }

.webfooter .inner .info ul {
  width: 240px;
  margin: 0 auto 5px; }

.webfooter .inner .info ul li {
  float: left;
  width: 80px;
  background: url("../../images/nav_footer_line.gif") 0 center no-repeat; }

.webfooter .inner .info ul li a {
  color: #fff;
  display: block;
  text-align: center;
  line-height: 20px;
  height: 20px;
  overflow: hidden; }

.webfooter .inner .info ul li a:hover {
  color: #ddd; }

.webfooter .inner .info ul li.f {
  background: none; }

.webfooter .inner .info p {
  display: block;
  text-align: center;
  color: #4e4e4e;
  font-size: 12px;
  line-height: 20px; }

.webfooter .inner .qr {
  float: right;
  height: 53px;
  background: url("../../images/footer_qr.gif") 0 13px no-repeat;
  padding: 30px 0 0 80px;
  width: 80px; }

.webfooter .inner .qr p {
  color: #fff;
  font-size: 14px;
  line-height: 18px; }

.webfooter .inner .qr span {
  color: #707070;
  font-size: 12px;
  line-height: 18px; }

/*registeralert*/
.registeralert {
  display: none;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3); }

.registeralert-main {
  position: relative;
  width: 520px;
  height: 455px;
  padding-top: 35px;
  margin: 0 auto;
  margin-top: 400px;
  border: 5px solid black;
  background-color: #fff; }

.registeralert-main-item {
  height: 40px;
  width: 330px;
  margin-left: 100px;
  margin-top: 20px;
  line-height: 40px; }

.registeralert-main-item label {
  position: absolute;
  margin-left: -60px; }

.registeralert-main-item input {
  height: 35px;
  width: 99%;
  text-indent: 10px;
  border: 1px solid #ccc;
  background-color: #fff; }

.registeralert-main-item input[type=submit] {
  background-color: #000;
  color: #fff; }

.registeralert-main-item .idcode1 {
  float: left;
  width: 150px;
  margin-top: 2px; }

.registeralert-main-item .pic {
  float: right;
  margin-right: 40px;
  margin-top: 2px; }

.registeralert-main-item .pic img {
  border: 1px solid #ccc; }

.registeralert-main-item .reflesh {
  position: absolute;
  overflow: hidden;
  width: 35px;
  height: 35px;
  margin-left: 140px;
  margin-top: 2px;
  border: 1px solid #ccc; }

.registeralert-main-item .reflesh img {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: all 1s linear; }

.registeralert-main-item .reflesh img:hover {
  transform: rotate(360deg); }

.registeralert-main-item .getfreemesg {
  position: absolute;
  margin-left: -100px;
  font-size: 12px;
  cursor: pointer; }

.registeralert-main-item .my-checkbox {
  float: left;
  margin-top: 12px; }

.registeralert-main-item .my-checkbox-active {
  float: left;
  margin-top: 12px; }

.registeralert-main-item .agree {
  float: left;
  font-size: 12px;
  color: #ccc; }

.registeralert-main-item .agree em {
  color: #000; }

.registeralert-main-item .login {
  float: right;
  font-size: 12px;
  cursor: pointer; }

.registeralert-main-item .login-weibo {
  float: right;
  width: 40px;
  height: 30px;
  margin-left: 10px;
  background: url("../../images/sprite_login.jpg") 0 -30px no-repeat;
  cursor: pointer; }

.registeralert-main-item .login-weichat {
  float: right;
  width: 40px;
  height: 30px;
  background: url("../../images/sprite_login.jpg") 0 0 no-repeat;
  cursor: pointer; }

.registeralert-main .close {
  position: absolute;
  width: 19px;
  height: 19px;
  margin-left: 480px;
  margin-top: -15px;
  background: url("../images/icon_close.jpg") 0 0 no-repeat;
  cursor: pointer; }

